import { money } from "./utils";
import { phone } from "phone";
import { useState } from "react";

const user = JSON.parse(
  localStorage.getItem("USER") || '{ "name": "", "number": "+54" }'
);

export const Confirmation = ({ selection, setFinished, onSubmit }) => {
  const [name, setName] = useState(user.name);
  const [number, setNumber] = useState(user.number);
  const [comment, setComment] = useState("");
  console.log(phone(number));
  return (
    <div>
      <h3 className="title">
        Total:{" "}
        {money(
          Object.keys(selection).reduce(
            (total, current) =>
              total +
              Object.values(selection[current]).reduce(
                (total, { cost, amount }) => total + cost * amount,
                0
              ),
            0
          )
        )}
      </h3>
      <div class="field">
        <label class="label">Nombre</label>
        <div class="control">
          <input
            onChange={({ target }) => {
              setName(target.value);
            }}
            value={name}
            class="input"
            type="text"
            placeholder="Quien retira?"
          />
        </div>
      </div>
      <div class="field">
        <label class="label">Numero de Whatsapp</label>
        <div class="control">
          <input
            onChange={({ target }) => {
              setNumber(target.value);
            }}
            type="text"
            value={number}
            class="input"
            placeholder="Whatsapp"
          />
        </div>
      </div>

      <div class="field">
        <label class="label">Aclaracion</label>
        <div class="control">
          <textarea
            onChange={({ target }) => {
              setComment(target.value);
            }}
            value={comment}
            class="textarea"
            placeholder="Por si nos queres aclarar algo..."
          ></textarea>
        </div>
      </div>

      <div class="field is-grouped">
        <div class="control">
          <button
            class="button"
            onClick={() => {
              if (!phone(number).isValid) {
                return alert("El numero de telefono no es correcto ");
              }
              localStorage.setItem("USER", JSON.stringify({ name, number }));
              alert(
                "Tu pedido fue enviado, te vamos a mandar un WhatsApp para confirmar! "
              );
              onSubmit();
            }}
          >
            Pedir
          </button>
        </div>
        <div class="control">
          <button
            class="button is-danger"
            onClick={() => {
              setFinished(false);
            }}
          >
            Cancelar
          </button>
        </div>
      </div>
    </div>
  );
};
